.card {
  padding: 54px 30px 78px;
  background-color: $color-default-white;
  border: 7px solid transparent;

  @include vp-1199 {
    padding: 56px 30px 78px;
  }

  @include vp-767 {
    padding: 43px 30px 70px;
  }

  &:hover {
    border-image: linear-gradient(135deg, #d10a42 0%, #00359f 100%);
    border-image-slice: 1;

    .card__button {
      outline: 0;
      color: $color-default-white;
      background: linear-gradient(97.05deg, #ed0335 17.6%, #003eb7 95.31%);
    }
  }
}

.card__title {
  margin: 0 0 12px;
  font-weight: 700;
  font-size: 26px;
  line-height: 29px;
  color: $color-sapphire;

  @include vp-767 {
    margin: 0 0 9px;
    font-size: 22px;
    line-height: 24px;
  }
}

.card__description {
  margin: 0 0 52px;
  font-size: 16px;
  line-height: 24px;
  color: #495a7d;

  @include vp-767 {
    margin: 0 0 33px;
  }
}

.card__price {
  position: relative;
  margin: 0 auto 117px;

  @include vp-767 {
    margin: 0 auto 101px;
  }

  &::before {
    content: attr(data-parent);
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 0;
    font-weight: 700;
    font-size: 100px;
    line-height: 69px;
    color: #e8e8e8;
    opacity: 0.48;

    @include vp-767 {
      top: -24px;
      font-size: 80px;
      line-height: 120px;
    }
  }
}

.card__button {
  padding: 28px 44px 20px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-sapphire;
  outline: 2px solid $color-sapphire;

  @include vp-767 {
    padding: 23px 44px 25px;
  }
}
